import React, { useState, useContext } from 'react';
import { navigate } from "@reach/router";
import { makeStyles } from '@material-ui/core/styles';
import Typography from '@material-ui/core/Typography';
import Button from '@material-ui/core/Button';
import AvailableContent from '../../data/availableContent.json';
import BookChapterList from '../../data/BookChapterList.json';
import VerseCounts from '../../data/verseCounts.json';
import AppContext from '../../contextAPI/AppContext'
import GetSegment from '../../scripts/NavigateToSegment'
import Search from '@material-ui/icons/Search';
import scrollToElement from 'scroll-to-element'

const useStyles = makeStyles(theme => ({
  root: {
    display: 'flex',
    flexWrap: 'wrap',
    justifyContent: 'space-around',
    overflow: 'hidden',
    backgroundColor: theme.palette.background.paper,
    width: '75vw',
    height: 'auto',
    overflowY: 'auto'
  },
  bookOptions: {
    display: 'flex',
    width: '75vw',
    justifyContent: 'center',
    flexWrap: 'wrap',
    padding: '2px'
  },
  button: {
    margin: '3px',
    width: '15px'
  },
  actionPanel: {
    margin: '15px',
    display: 'flex',
    flexDirection: 'row',
    justifyContent: 'space-between',
    '& Button': {
        margin: '0 5px'
      }
  }
}));

export default function ImageGridList(props) {
  const classes = useStyles();

  // -------------------------------------------------- //
  // -------------- STATE OF THE WEB APP -------------- //
  // -------------------------------------------------- //
  const [ selectedBook, updateSelectedBook ] = useState('')
  const [ chapters, updateChapters ] = useState(['error'])
  const [ verses, updateVerses ] = useState(['error'])
  const [ selectedChapter, updateSelectedChapter ] = useState(0)
  const [ selectedVerse, updateSelectedVerse ] = useState(0)
  const receivedAppContext = useContext(AppContext)

  const books = Object.keys(AvailableContent[receivedAppContext.language][receivedAppContext.version])

  const pathSplit = props.currentLocation.split('/')
  console.log(pathSplit)
  const currentSegment = pathSplit[3]
  const selectBook = (book) => {
    updateSelectedBook(book)
    updateChapters(BookChapterList[book].chapters.split(','))
  }
  
  const deselectBook = () => {
    updateSelectedBook('')
    updateSelectedChapter(0)
    updateSelectedVerse(0) 
  }

  const selectChapter = (chapter) => {
    updateSelectedChapter(parseInt(chapter))
    const verseNums = []
    for (let i=0; i<VerseCounts[selectedBook][chapter]; i++) {
      verseNums.push(`${1+i}`)
    }
    updateVerses(verseNums)
  }

  const deselectChapter = () => {
    updateSelectedChapter(0)
    updateSelectedVerse(0) 
  }

  const selectVerse = (verse) => {
    updateSelectedVerse(parseInt(verse))
  }

  const deselectVerse = () => {
    updateSelectedVerse(0) 
  }

  const search = () => {
    const segment = GetSegment(selectedBook, selectedChapter, selectedVerse)
    receivedAppContext.updateDisplayRef(true)
    receivedAppContext.updateSearchID(`${selectedBook}-${selectedChapter}-${selectedVerse}`)
    console.log("Location: ", currentSegment)
    console.log("Function: ", segment)
    if (currentSegment === segment) {
      receivedAppContext.updateSearchMenu()
      samePageNavigation(`#${selectedBook}-${selectedChapter}-${selectedVerse}`)
    } else {
      receivedAppContext.updateSearchMenu()
      navigate(`/${receivedAppContext.language}/${receivedAppContext.version}/${segment}`,
        { state: { currentInterlinear: receivedAppContext.interlinear }})
    }
  }

  const samePageNavigation = (id) => {
    scrollToElement(id, {
        offset: -65,
        ease: 'inOutCube',
        duration: 1500
    });
    receivedAppContext.updateSearchID('')
  }

  return (
    <div className={classes.root}>
      <Typography display="block" >Select Book</Typography>
      {(selectedBook === '') ? (<div className={classes.bookOptions}>
        {books.map(book => (<Button color='secondary' variant="outlined" key={book} onClick={() => selectBook(book)}>{book}</Button>))}
      </div>) : (<div className={classes.bookOptions}>
        <Button color='secondary' variant="contained" key={selectedBook} onClick={() => deselectBook()}>{selectedBook}</Button>
      </div>)}
      {(selectedBook !== '') ? (
        <React.Fragment>
          <Typography display="block" >Select Chapter</Typography>
          <div className={classes.bookOptions}>
            {(selectedChapter === 0) ? chapters.map(chapter => (<Button color='secondary' variant="outlined" key={chapter} onClick={() => selectChapter(parseInt(chapter))}>{chapter}</Button>)) : <Button color='secondary' variant="contained" key={selectedChapter} onClick={() => deselectChapter()}>{selectedChapter}</Button>}
          </div>
        </React.Fragment>) : null}
      {(selectedBook !== '' && selectedChapter !== 0) ? (
        <React.Fragment>
          <Typography display="block" >Select Verse</Typography>
          <div className={classes.bookOptions}>
            {(selectedVerse === 0) ? verses.map(verse => (<Button color='secondary' variant="outlined" key={verse} onClick={() => selectVerse(parseInt(verse))}>{verse}</Button>)) : <Button color='secondary' variant="contained" key={selectedVerse} onClick={() => deselectVerse()}>{selectedVerse}</Button>}
          </div>
        </React.Fragment>) : null}
        {(selectedVerse !== 0) ? (<div className={classes.actionPanel}>
          <Button color='primary' variant="outlined" onClick={() => deselectBook()}>CLEAR</Button>
          <Button color='primary' variant="contained" onClick={() => search()}><Search/></Button>
        </div>) : null}
    </div>
  );
}