import React, { useContext } from 'react'
import { Link } from "gatsby"
import AppContext from '../../contextAPI/AppContext'

import { makeStyles } from '@material-ui/styles';
import IconButton from '@material-ui/core/IconButton';
import PlayCircleFilled from '@material-ui/icons/PlayCircleFilled';

const useStyles = makeStyles({
    link: {
        position: 'fixed',
        bottom: '0.3em',
        paddingTop: '0em',
        paddingBottom: '0.14em'
    },
    left: {
        left: '0.3em',
        paddingLeft: '0.13em',
        paddingRight: '0.17em'
    },
    right: {
        right: '0.3em',
        paddingLeft: '0.17em',
        paddingRight: '0.13em'
    },
    icon: {
        backgroundColor: 'white', 
        color: '#3A4C7D', 
        borderRadius: '25px',
        fontSize: '40px',

    }
})

const IconButtons = (props) => {
    const receivedAppContext = useContext(AppContext)
    const classes = useStyles();
    return (
        <React.Fragment>
            <Link to={props.prev} 
                className={`${classes.link} ${classes.left}`}  
                state={{
                    currentDisplayRef: receivedAppContext.displayRef,
                    currentInterlinear: receivedAppContext.interlinear}}
            >
                <IconButton>
                    <PlayCircleFilled 
                        className={classes.icon}
                        style={{transform: 'rotate(180deg)'}} />
                </IconButton>
            </Link>
            <Link to={props.next} 
                className={`${classes.link} ${classes.right}`}
                state={{
                    currentDisplayRef: receivedAppContext.displayRef,
                    currentInterlinear: receivedAppContext.interlinear}}
            >
                <IconButton>
                    <PlayCircleFilled 
                        className={classes.icon}/>
                </IconButton>
            </Link>
        </React.Fragment>
    )
}

export default IconButtons; 