import React, { useContext } from 'react'
import AppContext from '../../contextAPI/AppContext'
import styles from './WordDataTable.module.css'

const Table = () => {
        const receivedAppContext = useContext(AppContext)
        const displayedData = (receivedAppContext.language === 'GRK') ? [
                ['Gloss', receivedAppContext.dialog.content.IT],
                ['Lemma', receivedAppContext.dialog.content.lexeme],
                ['SBL Transliteration', receivedAppContext.dialog.content.transSBL],
                ['GK #', receivedAppContext.dialog.content.GoodrickKohlenbergerNumbers],
                ['LouwNida #', receivedAppContext.dialog.content.LouwNidaNumbers]
         ] : [
                ['Gloss', receivedAppContext.dialog.content.ETCBCgloss],
                ['Lemma', receivedAppContext.dialog.content.HebrewLexeme],
                ['SBL Transliteration', receivedAppContext.dialog.content.SBLstyleTransliteration],
                ['Hebrew Reference', `${receivedAppContext.dialog.content.Heb_ref.book_id} ${receivedAppContext.dialog.content.Heb_ref.chapter_id}:${receivedAppContext.dialog.content.Heb_ref.verse_id}`],
                ['KJV Reference', `${receivedAppContext.dialog.content.KJV_ref.book_id} ${receivedAppContext.dialog.content.KJV_ref.chapter_id}:${receivedAppContext.dialog.content.KJV_ref.verse_id}`],
                ['Morph Details', receivedAppContext.dialog.content.morphologyDetail]
         ]
        return (
        <table className={styles.dialogTable}><tbody>
                {displayedData.map(row => (<tr className={styles.row}><td className={styles.leftCell}>{row[0]}</td><td className={styles.rightCell}>{(typeof row[1] === "object")?`${row[1].book_id} ${row[1].chapter_id}:${row[1].verse_id}`:row[1]}</td></tr>))}
                </tbody>
        </table>
        )
}

export default Table;