import React, { useContext }  from 'react';
import { navigate } from "@reach/router";
import AppContext from '../../contextAPI/AppContext'
import clsx from 'clsx';
import Select from 'react-select';
import { emphasize, makeStyles, useTheme } from '@material-ui/core/styles';
import Typography from '@material-ui/core/Typography';
import NoSsr from '@material-ui/core/NoSsr';
import TextField from '@material-ui/core/TextField';
import Paper from '@material-ui/core/Paper';
import Chip from '@material-ui/core/Chip';
import MenuItem from '@material-ui/core/MenuItem';
import CancelIcon from '@material-ui/icons/Cancel';
import PropTypes from 'prop-types';
import LanguageVersionInfo from '../../data/LangsVersions.json'
import AvailableContent from '../../data/availableContent.json'

const useStyles = makeStyles(theme => ({
  root: {
    flexGrow: 1,
    height: 'auto',
  },
  input: {
    display: 'flex',
    padding: 0,
    height: 'auto',
  },
  valueContainer: {
    display: 'flex',
    flexWrap: 'wrap',
    flex: 1,
    alignItems: 'center',
    overflow: 'hidden',
  },
  chip: {
    margin: theme.spacing(0.5, 0.25),
  },
  chipFocused: {
    backgroundColor: emphasize(
      theme.palette.type === 'light' ? theme.palette.grey[300] : theme.palette.grey[700],
      0.08,
    ),
  },
  noOptionsMessage: {
    padding: theme.spacing(1, 2),
  },
  singleValue: {
    fontSize: 16,
  },
  placeholder: {
    position: 'absolute',
    left: 2,
    bottom: 6,
    fontSize: 16,
  },
  paper: {
    position: 'absolute',
    zIndex: 1,
    marginTop: theme.spacing(1),
    left: 0,
    right: 0,
  },
  divider: {
    height: theme.spacing(2),
  },
}));

function NoOptionsMessage(props) {
  return (
    <Typography
      color="textSecondary"
      className={props.selectProps.classes.noOptionsMessage}
      {...props.innerProps}
    >
      {props.children}
    </Typography>
  );
}

NoOptionsMessage.propTypes = {
  children: PropTypes.node,
  innerProps: PropTypes.object,
  selectProps: PropTypes.object.isRequired,
};

function inputComponent({ inputRef, ...props }) {
  return <div ref={inputRef} {...props} />;
}

inputComponent.propTypes = {
  inputRef: PropTypes.oneOfType([PropTypes.func, PropTypes.object]),
};

function Control(props) {
  const {
    children,
    innerProps,
    innerRef,
    selectProps: { classes, TextFieldProps },
  } = props;

  return (
    <TextField
      fullWidth
      InputProps={{
        inputComponent,
        inputProps: {
          className: classes.input,
          ref: innerRef,
          children,
          ...innerProps,
        },
      }}
      {...TextFieldProps}
    />
  );
}

Control.propTypes = {
  children: PropTypes.node,
  innerProps: PropTypes.object,
  innerRef: PropTypes.oneOfType([PropTypes.func, PropTypes.object]),
  selectProps: PropTypes.object.isRequired,
};

function Option(props) {
  return (
    <MenuItem
      ref={props.innerRef}
      selected={props.isFocused}
      component="div"
      style={{
        fontWeight: props.isSelected ? 500 : 400,
      }}
      {...props.innerProps}
    >
      {props.children}
    </MenuItem>
  );
}

Option.propTypes = {
  children: PropTypes.node,
  innerProps: PropTypes.object,
  innerRef: PropTypes.oneOfType([PropTypes.func, PropTypes.object]),
  isFocused: PropTypes.bool,
  isSelected: PropTypes.bool,
};

function Placeholder(props) {
  return (
    <Typography
      color="textSecondary"
      className={props.selectProps.classes.placeholder}
      {...props.innerProps}
    >
      {props.children}
    </Typography>
  );
}

Placeholder.propTypes = {
  children: PropTypes.node,
  innerProps: PropTypes.object,
  selectProps: PropTypes.object.isRequired,
};

function SingleValue(props) {
  return (
    <Typography className={props.selectProps.classes.singleValue} {...props.innerProps}>
      {props.children}
    </Typography>
  );
}

SingleValue.propTypes = {
  children: PropTypes.node,
  innerProps: PropTypes.object,
  selectProps: PropTypes.object.isRequired,
};

function ValueContainer(props) {
  return <div className={props.selectProps.classes.valueContainer}>{props.children}</div>;
}

ValueContainer.propTypes = {
  children: PropTypes.node,
  selectProps: PropTypes.object.isRequired,
};

function MultiValue(props) {
  return (
    <Chip
      tabIndex={-1}
      label={props.children}
      className={clsx(props.selectProps.classes.chip, {
        [props.selectProps.classes.chipFocused]: props.isFocused,
      })}
      onDelete={props.removeProps.onClick}
      deleteIcon={<CancelIcon {...props.removeProps} />}
    />
  );
}

MultiValue.propTypes = {
  children: PropTypes.node,
  isFocused: PropTypes.bool,
  removeProps: PropTypes.object.isRequired,
  selectProps: PropTypes.object.isRequired,
};

function Menu(props) {
  return (
    <Paper square className={props.selectProps.classes.paper} {...props.innerProps}>
      {props.children}
    </Paper>
  );
}

Menu.propTypes = {
  children: PropTypes.node,
  innerProps: PropTypes.object,
  selectProps: PropTypes.object,
};

const components = {
  Control,
  Menu,
  MultiValue,
  NoOptionsMessage,
  Option,
  Placeholder,
  SingleValue,
  ValueContainer,
};

export default function IntegrationReactSelect(props) {
  const receivedAppContext = useContext(AppContext)
  const classes = useStyles();
  const theme = useTheme();
  const [single, setSingle] = React.useState(null);
  if (single === null) {
    setSingle({
      label: LanguageVersionInfo[receivedAppContext.language].versions[receivedAppContext.version],
      value: receivedAppContext.version
    });
  } else if (single.value !== receivedAppContext.version) {
    setSingle({
      label: LanguageVersionInfo[receivedAppContext.language].versions[receivedAppContext.version],
      value: receivedAppContext.version
    });
  }

  const getLangOptions = () => {
    let LanguageOptions = []
    const languageKeys = Object.keys(LanguageVersionInfo)
    languageKeys.forEach(key => {
        const langObj = {
            label: LanguageVersionInfo[key].language_name,
            value: key
        }
        LanguageOptions.push(langObj)
    })
    return LanguageOptions
  }

  function handleChangeSingle(value) {
    setSingle(value);
    updateVersion(value.value);
  }

  const updateVersion = (value) => {
    receivedAppContext.updateVersion(value)
    receivedAppContext.updateVersionSelect(false)
    if (props.location.pathname !== '/') {
      const currentSegment = props.location.pathname.split('/')[3]
      receivedAppContext.updateInterlinear(false)
      const firstBookOfNewLanguage = Object.keys(AvailableContent[receivedAppContext.language][value])[0]
      const firstSegmentOfNewLanguage = AvailableContent[receivedAppContext.language][value][firstBookOfNewLanguage].segments[1]
      const path = (AvailableContent[receivedAppContext.language][value][currentSegment] !== undefined) ? 
      `/${receivedAppContext.language}/${value}/${currentSegment}`
      // `/${event.value}/${getVersionOptions(event.value)[0].value}/${receivedAppContext.segmentID}` 
      : `/${receivedAppContext.language}/${value}/${firstSegmentOfNewLanguage}`
      navigate(path)
    }
  }

  // const updateLang = (value) => {
  //   receivedAppContext.updateLanguage(value)
  //   receivedAppContext.updateVersion(getVersionOptions(value)[0].value)
  //   if (getVersionOptions(value).length < 2) {
  //       receivedAppContext.updateLanguageSelect(false)
  //       if (props.location.pathname !== '/') {
  //         const currentSegment = props.location.pathname.split('/')[3]
  //         receivedAppContext.updateInterlinear(false)
  //         const firstBookOfNewLanguage = Object.keys(AvailableContent[value][getVersionOptions(value)[0].value])[0]
  //         const firstSegmentOfNewLanguage = AvailableContent[value][getVersionOptions(value)[0].value][firstBookOfNewLanguage].segments[1]
  //         const path = (AvailableContent[value][getVersionOptions(value)[0].value][currentSegment] !== undefined) ? 
  //         `/${value}/${getVersionOptions(value)[0].value}/${currentSegment}`
  //         // `/${event.value}/${getVersionOptions(event.value)[0].value}/${receivedAppContext.segmentID}` 
  //         : `/${value}/${getVersionOptions(value)[0].value}/${firstSegmentOfNewLanguage}`
  //         navigate(path)
  //       }
  //   } else {
  //       // revealVersionSelect()
  //   }
  // }

  const getVersionOptions = (langKey) => {
    let VersionOptions = []
    const versionKeys = Object.keys(LanguageVersionInfo[langKey].versions)
    versionKeys.forEach(key => {
        const versionObj = {
            label: LanguageVersionInfo[langKey].versions[key],
            value: key
        }
        VersionOptions.push(versionObj)
    })
    return VersionOptions
  }

  const selectStyles = {
    input: base => ({
      ...base,
      color: theme.palette.text.primary,
      '& input': {
        font: 'inherit',
      },
    }),
  };

  return (
    <div className={classes.root}>
      <NoSsr>
        <Select
          classes={classes}
          styles={selectStyles}
          inputId="react-select-single"
          TextFieldProps={{
            label: 'Version',
            InputLabelProps: {
              htmlFor: 'react-select-single',
              shrink: true,
            },
            placeholder: 'Search Versions',
          }}
          options={getVersionOptions(receivedAppContext.language)}
          components={components}
          value={single}
          onChange={handleChangeSingle}
        />
      </NoSsr>
    </div>
  );
} 